    // $widthContent: 960px;
$widthPageMax: none;
$widthPageMin: 0;

* {
    margin: 0;
    padding: 0;
}

html {
    height: 100%;
}

header,
nav,
section,
article,
aside,
footer {
    display: block;
}

body {
    width: 100%;
    height: 100%;
    font-family: 'ProximaNovaLight';
    font-size: 14px;
    line-height: 21px;
}

img {
    border: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
}

div {
    margin: 0 auto;
}

a {
    color: #00af42;
    outline: none;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

input {
    vertical-align: middle;
}


/* UTIL */ 

.clr {
    clear: both;
    height: 0;
    line-height: 0;
    display: block;
    float: none;
    padding: 0;
    margin: 0;
    border: none;
    font: 0px/0px;
}

.fleft {
    float: left;
}

.fright {
    float: right;
}

.tcent {
    text-align: center;
}

.tleft {
    text-align: left;
}

.tright {
    text-align: right;
}


/* headings */

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.1;
    font-family: 'ProximaNovaBold';
}
h1 {
    font-size: 36px;
    color: #1d1d1b;
    padding-bottom: 20px;
}


/* Main */

.main {
    position: relative;
    min-height: 100%;
    background: #fff;
    &.main-inner {
        background: #fff; 
    }
}


.content {
    position: relative;
}

// .wrapper {
//     padding: 0px 0px;
// }

.left-col {
    float: left;
    width: 256px;
    right: 768px;
    position: relative;
}

.middle-col {
    position: relative;
    left: 256px;
    width: 768px;
    float: left;
    font-size: 16px;
    line-height: 24px;
    font-family: 'ProximaNovaReg';
    img {
        max-width: 100%;
    }
    ol {
        li {
            list-style: none;
            counter-increment: step-counter;
            position: relative;
            padding-bottom: 20px;
            &:before {
                position: absolute;
                top: 0;
                left: -40px;
                content: counter(step-counter, decimal);
                color: #00af42;
                font-size: 16px;
                font-family: 'ProximaNovaBold';
            }
        }
    }
}
.popup-bg {
    background: rgba(0,0,0,0.3);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;
}

.inner-right-col {
    float: right;
    width: 256px;
}
.inner-left-col {
    font-family: 'ProximaNovaReg';
    font-size: 16px;
    float: left;
    width: 728px;
}

.jq-number {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding: 0 32px 0;
    height: 32px;
    width: 121px;
    background: #f2f2f2;
    border: 1px solid #f2f2f2;
}
.jq-number__field {
    width: 53px;
    background: #fff;
}
.jq-number__field:hover {

}
.jq-number__field input {
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    border: none;
    font-size: 14px;
    font-family: 'ProximaNovaBold';
    outline: none;
    background: none;
    color: #231f20;
    text-align: center; /* для Opera Presto */
    -moz-appearance: textfield;
}
.jq-number__field input::-webkit-inner-spin-button,
.jq-number__field input::-webkit-outer-spin-button {
    margin: 0; /* в каких-то браузерах присутствует отступ */
    -webkit-appearance: none;
}
.jq-number__spin {
    cursor: pointer;
    position: absolute;
    top: 0;
    right: 0;
    width: 32px;
    border: none; 
    height: 30px;
}
.jq-number__spin.minus {
    top: 0;
    left: 0;
    right: auto;
}
.jq-number__spin:hover {
    background: #00af42;
    color: #fff
}
.jq-number__spin:active {
    background: #00af42;
}
