.middle-col img { max-width: 100%; }

.middle-col ol li { list-style: none; counter-increment: step-counter; position: relative; padding-bottom: 20px; }

.middle-col ol li:before { position: absolute; top: 0; left: -40px; content: counter(step-counter, decimal); color: #00af42; font-size: 16px; font-family: 'ProximaNovaBold'; }

