.inner-left-col { font-family: 'ProximaNovaReg'; font-size: 16px; float: left; width: 728px; }

.jq-number { position: relative; display: inline-block; vertical-align: middle; padding: 0 32px 0; height: 32px; width: 121px; background: #f2f2f2; border: 1px solid #f2f2f2; }

.jq-number__field { width: 53px; background: #fff; }

.jq-number__field input { box-sizing: border-box; width: 100%; height: 30px; border: none; font-size: 14px; font-family: 'ProximaNovaBold'; outline: none; background: none; color: #231f20; text-align: center; /* для Opera Presto */ -moz-appearance: textfield; }

