.jq-number__spin { cursor: pointer; position: absolute; top: 0; right: 0; width: 32px; border: none; height: 30px; }

.jq-number__spin.minus { top: 0; left: 0; right: auto; }

.jq-number__spin:hover { background: #00af42; color: #fff; }

.jq-number__spin:active { background: #00af42; }

.jq-number__spin.minus:hover:after { color: #fff; }

.jq-number__spin.plus:hover:after { color: #fff; }

